.createContainer { 
    padding: 40px 0px;
    position: absolute;
    height: 100%;
    width: 100%;
    background: var(--color_background);
}

.foregroundForm {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 80%;
    max-width: 500px;
    /*background: #7d9eb5;*/
    background: #F8F9FA;
    padding: 10px;
}

.promptDiv {
    text-align: center;
    margin-top: 10px;
    width: 100%;
    font: 18px Arial, sans-serif;
    color: var(--color_default_text);
}

#userBtn {
    display: block;
    margin-top: 30px;
    width: 200px;
    height: 25px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;
}

#userBtn:hover {
    background: var(--color_hover)
}

#createDiv {
    margin-top: 25px;
}

#passReq {
    width: 100%;
    margin-top: 20px;
    font: 18px Arial, sans-serif;
    color: var(--color_default_text);
}

#passReqList {
    margin-left: auto;
    margin-right: auto;
    width: 90%;
    list-style-position: inside;
}

#passReqList p {
    text-align: center;
}

#passReqList li {
    padding: 5px 0px;
}

.emptyDiv {
    height: 100px;
}

