#dashContainer {
    background: var(--color_background);
}

#dashboardBox {
    margin-top: -30px;
}

.dashCompDiv {
    border: 3px solid var(--color_background);
    border-radius: 25px;
    padding: 10px 30px;
    text-align: center;
    background: white;
}

.dashCompDiv table {
    margin-left: auto;
    margin-right: auto;
}

.dashCompDiv table td, th {
    padding: 7.5px;
    padding-block: 6px;
}

#deviceConnect {
    width: 70%;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
    background: white;
    border-radius: 25px;
    padding: 10px 5px;
}

@media screen and (max-width: 599px) {
    #deviceConnect Button {
        margin-left: auto;
        margin-right: auto;
    }
}

@media screen and (min-width: 600px) {
    #deviceConnect FormControl {
        float: left;
    }
    #deviceConnect Button {
        float: right
    }
}

#door{
    height: 150px;
}

#lightbulb {
    width: 50px;
}

#colorSel {
    width: 80%;
    height: 150px;
    margin-left: auto;
    margin-right: auto;
}

#lightScheduleDiv {
    margin-top: 25px;
}

.timeSel {
    margin: 15px;
}

#tempEntry {
    width: 75px;
    display: block;
}

#currentTempData {
    margin-left: auto;
    margin-right: auto;
    color: var(--color_background);
    width: 150px;
}

#curTemp {
    font-size: 40px;
    margin: -5px;
}

#timeDisp {
    font: 30px Arial, sans-serif;
    color: white;
    padding-top: 20px;
    background: var(--color_background);
    text-align: center;
}

.powerHeading {
    margin: 10px 0px 0px 0px;
}