#demoContainer {
    padding-top: 25px;
    background: var(--color_background);
}

#demoBox {
    margin-top: -30px;
}

.demoCompDiv {
    border: 3px solid var(--color_background);
    border-radius: 25px;
    margin: 40px;
    padding: 5px 10px;
    text-align: center;
    background: white;
}

#videoContainer {
    width: 80%;
    background: white;
    border-radius: 25px;
    padding: 10px;
    margin-left: auto;
    margin-right: auto;
}

#videoResponsive {
    border-radius: 25px;
    overflow: hidden;
    padding-bottom: 56.25%;
    position: relative;
    height: 0;
    margin-left: auto;
    margin-right: auto;
}
  
#videoResponsive iframe {
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    position: absolute;
}

#stm {
    flex: 1;
    padding-top: 10px;
    margin-top: 20px;
    width: 100%;
    height: 100%;
    object-fit: cover;
}