.descContainer {
    display: block;
    margin-top: 50px;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    background: var(--color_background);
    padding: 10px;
    position: absolute;
    height: 100%;
}

.descDiv {
    width: 60%;
    margin-left: auto;
    margin-right: auto;
}

.descP {
    margin-top: 25px;
    text-align: center;
}

.descList {
    margin-left: 10px;
}

.loginBtn {
    display: block;
    margin-top: 20px;
    width: 200px;
    background-color: var(--color_schemeB);
    text-align: center;
    margin: 20px auto;
    padding: 10px 10px;
    text-decoration: none;
    color: var(--color_default_text);
}

.loginBtn:hover {
    background: var(--color_hover);
}

.authorTable {
    margin-top: 25px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}

.authorTable td {
    padding: 5px;
}

.card {
    margin-left: auto;
    margin-right: auto;
    max-width: 400px;
    margin-bottom: 100px;
}