.profileContainer { 
    padding: 40px 0px;
    position: absolute;
    background: var(--color_background);
    width: 100%;
    height: 100%;
}

.promptLabel {
    color: var(--color_default_text);
    text-align: center;
    font-size: 16px;
    padding-bottom: 10px;
}

.profileSectionHeader {
    color: var(--color_default_text_light);
    text-align: center;
    font-size: 20px;
    margin-top: 80px;
    margin-bottom: 25px;
}

.profileForegroundDiv {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 70%;
    max-width: 800px;
    background: #7d9eb5;
    padding: 10px;
}

.promptDiv {
    margin-top: 20px;
    font: 18px Arial, sans-serif;
    color: var(--color_default_text);
}

#usernameInput, #fnameInput, #lnameInput, 
#newPasswordInput, #curPasswordInput {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 80%;
    font: 18px Arial, sans-serif;
    height: 30px;
}

#saveBtn {
    display: block;
    margin-top: 20px;
    width: 200px;
    height: 25px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;
    padding-bottom: 25px;
}

#saveBtn:hover {
    background: var(--color_hover);
}