.deviceContainer { 
    padding: 0px 20px;
    position: absolute;
    background: var(--color_background);
    width: 100%;
    height: 100%;
}

#deviceTable {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    background: white;
    overflow-x: scroll;
}

.devNameCol {
    max-width: 20%
}

.devIDCol, .devTokenCol {
    width: 40%;
}

.devBtnCol {
    width: 10px;
}

.promptLabel {
    color: var(--color_default_text);
    text-align: center;
    font-size: 16px;
    padding-bottom: 10px;
}

.deviceSectionHeader {
    color: var(--color_default_text_light);
    text-align: center;
    font-size: 20px;
    margin-top: 40px;
    margin-bottom: 25px;
}

.deviceForegroundDiv {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 70%;
    max-width: 800px;
    background: #7d9eb5;
    padding: 10px;
}

.promptDiv{
    margin-top: 20px;
    font: 18px Arial, sans-serif;
    color: var(--color_default_text);
}

#idInput, #tokenInput {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 80%;
    font: 18px Arial, sans-serif;
    height: 30px;
}

#saveBtn, #addMoreDeviceBtn{
    display: block;
    margin-top: 20px;
    width: 200px;
    height: 25px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;
    padding-bottom: 25px;
}

#saveBtn:hover, #addMoreDeviceBtn:hover {
    background: var(--color_hover);
}

.deviceWebhooks {
    width: 90%;
    max-width: 700px;
    background: white;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
}

.webhookSectionHeader {
    font: 16px Arial, sans-serif;
    padding-top: 20px;
    text-align: center;
}

#webhookUrl {
    font: 14px Arial, sans-serif;
    background: transparent;
    box-shadow: 0px 0px 0px transparent;
    border: 0px solid transparent;
    text-shadow: 0px 0px 0px transparent;
    margin: 5px 0px;
    padding: 0px;
}