 :root {
    --color_background: #557A95;
    --color_default_text: #050505;
    --color_default_text_light: #ffffff;
    --color_hover: #B1A296;

    --color_schemeA: #5D5C61;
    --color_schemeB: #938E93;
    --color_schemeC: #7395AE;
    --color_schemeD: #557A95;
    --color_schemeE: #B1A296;
}

html {
    background: var(--color_background);
}

.mainHeader {
    font-family: Georgia, serif;
    color: var(--color_default_text_light);
    padding-top: 40px;
    text-align: center;
}

.sectionHeader {
    color: var(--color_default_text_light);
    text-align: center;
    font-size: 20px;
    padding-top: 40px;
    margin-top: 25px;
    margin-bottom: 25px;
}

.topNavBar {
    background: #5D5C61;
}

.navButton {
    text-decoration: none;
    color: #ffffff;
}

.navButton:hover {
    color: #d9d9d9;
}

.navButtonDark {
    text-decoration: none;
    color: #000000;
}

.navButtonDark:hover {
    color: #000000;
}

.refDiv {

}

.userIcon {
    width: 20px;
}

#basic-nav-dropdown::after{
    display: none; 
}

.footerDiv {
    position: fixed;
    bottom: 0;
    width: 100%;
    background: #F8F9FA;
}

.pageNotFoundDiv {
    background: var(--color_background);
}

.pageNotFoundImg {
    padding-top: 100px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}