.loginContainer { 
    padding: 40px 0px;
    position: absolute;
    background: var(--color_background);
    width: 100%;
    height: 100%;
}

.foregroundDiv {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
    max-width: 500px;
    background: #7d9eb5;
    padding: 10px;
}

.promptDiv{
    margin-top: 20px;
    font: 18px Arial, sans-serif;
    color: var(--color_default_text);
}

#passwordInput, #usernameInput, #deviceInput {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 80%;
    font: 18px Arial, sans-serif;
    height: 30px;
}

.showPassDiv {
    text-align: center;
    color: var(--color_default_text_light);
    margin-top: 15px;
    font: 18px Arial, sans-serif;
}

.showPassDiv label {
    margin-right: 10px;
}

#userBtn {
    display: block;
    margin-top: 20px;
    width: 200px;
    height: 25px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;
    padding-bottom: 25px;
}

#userBtn:hover {
    background: var(--color_hover);
}

#createDiv {
    display: block;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: 25px;
    margin-bottom: 25px;
}

#createAccount {
    text-decoration: none;
    font-size: 18px;
    color: var(--color_default_text_light);
}

#createAccount:hover {
    color: var(--color_hover);
}
